import React, { useEffect, useState } from 'react';
import api from '../../api'

// import component
import { Table, Tag, Button, message, Modal } from 'antd'
import CreateQution from './CreateQution/CreateQution'
const { confirm } = Modal
const CodingQution = () => {
   const [qutions, setQutions] = useState(null)
   const [newQution, setNewQution] = useState(null)
   const [visible, setVisible] = useState(false)
   useEffect(() => {
      getData()
   }, [])
   const getData = async () => {
      let data = await api.coding.getCodingQutions();
      console.log(data)
      setQutions(data)
   }
   const deleteQution = async (id) => {
      confirm({
         title: '',
         content: '',
         async onOk() {
            let data = await api.coding.deleteCodingQutionById(id);
            if (data.success) {
               message.success("")
               getData()
            } else {
               message.error("")
            }
         },
         onCancel() {
            console.log('Cancel');
         },
         okText: "",
         cancelText: ""
      });
   }

   const columns = [
      {
         title: '',
         dataIndex: 'title',
         key: 'title',
      },
      {
         title: '',
         key: 'tags',
         render: (text, record) => (
            <div>
               {record.tags && record.tags.map(tag => (
                  <Tag color="#f50">{tag}</Tag>
               ))}
            </div>
         ),
      },
      {
         title: '',
         key: 'action',
         render: (text, record) => (
            <Button type="danger" onClick={() => deleteQution(record._id)}></Button>
         ),
         align: 'right'

      },
   ];
   const showModal = () => {
      setVisible(true)
   }

   const handleOk = async () => {
      if (newQution.title) {
         if (newQution.body) {
            if (newQution.testData && newQution.testData.length > 0) {
               let result = await api.coding.CreateQution(newQution)
               if (result.success) {
                  message.success("")
                  getData()
               } else {
                  message.error("")
               }
            } else {
               message.error("")
            }
         } else {
            message.error("")
         }
      }
      else {
         message.error("")
      }
      setVisible(false)
   }

   const handleCancel = () => {
      setVisible(false)
   }

   const handelCodingQutionChange = (data) => {
      setNewQution(data)
   }
   return (
      <div>
         <Button type="primary" onClick={showModal}>
            
        </Button>
         <Modal
            title=""
            visible={visible}
            onOk={handleOk}
            onCancel={handleCancel}
            width="70%"
         >
            <CreateQution onChange={handelCodingQutionChange}></CreateQution>
         </Modal>
         <Table
            dataSource={qutions}
            columns={columns}
            expandedRowRender={record => (
               <div style={{display:"flex",justifyContent:"space-between"}}>
                  <div dangerouslySetInnerHTML={{ __html: record.body }}>
                  </div>
                  <div style={{width:"50%",padding:"1rem",background:"#333"}}>
                     {record.testData.map(data=>(
                        <React.Fragment>
                           <h4 style={{color:"white"}}></h4>
                           <p style={{color:"white"}}></p>
                           <pre style={{color:"white"}}>
                              {data.input}
                           </pre>
                           <p style={{color:"white"}}></p>
                           <pre style={{color:"white"}}>
                              {data.output}
                           </pre>
                        </React.Fragment>
                     ))}
                  </div>
               </div>
            )}
         />;
      </div>
   );
};

export default CodingQution;